import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';

//Exercise Component
const Exercise = props => (
  <tr>
    <td>{props.exercise.username}</td>
    <td>{props.exercise.description}</td>
    <td>{props.exercise.duration}</td>
    <td>{props.exercise.date.substring(0,10)}</td>
    <td>
      <Link to={"/edit/"+props.exercise._id}>edit</Link> | <a href="#" onClick={() => { props.deleteExercise(props.exercise._id) }}>delete</a>
    </td>
  </tr>
)


//ExerciseList Component
export default class ExerciseList extends Component {
    
    constructor(props){
        super(props);

        //binding 'this' object 
        this.deleteExercise = this.deleteExercise.bind(this);
        
        this.state = {exercises: []}; 
    }

    componentDidMount(){
        axios.get('http://localhost:3000/exercises/')
        .then(response => {
            this.setState({exercises: response.data})
        })
        .catch((error => {
            console.log(error);
        }))
    }

    //delete method to delete the specific exercise with 'id' from the database
    deleteExercise(id) {
      axios.delete('http://localhost:3000/exercises/'+id)
        .then(response => { console.log(response.data)});

        //cheeky trick : using filter and (not returning the object with 'id')
        this.setState({
            exercises: this.state.exercises.filter(el => el._id !== id)
        })
    }
    
    
    //exercise list method: lists the rows in the exercise
    exerciseList() {
        return this.state.exercises.map(currentexercise => {
          return <Exercise exercise={currentexercise} deleteExercise={this.deleteExercise} key={currentexercise._id}/>;
        })
      }


    render() {
        return (
          <div>
            <h3>Logged Exercises</h3>
            <table className="table">
              <thead className="thead-light">
                <tr>
                  <th>Username</th>
                  <th>Description</th>
                  <th>Duration</th>
                  <th>Date</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                { this.exerciseList() }
              </tbody>
            </table>
          </div>
        )
      }
    }